import { defineComponent } from "vue";
import Tagify from "@yaireo/tagify";
import "@yaireo/tagify/dist/tagify.css";
import EventEmitter from "@/util/EventEmitter";

const tagify: any = null;

export default defineComponent({
  name: "Tags",
  props: {
    mode: String,
    settings: Object,
    name: String,
    id: String,
    blockId: String,
    value: [String, Array],
    onChange: Function,
  },
  data() {
    return {
      tagify,
      modifiedValue: this.$props.value,
    };
  },
  watch: {
    value(newVal) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      vm.tagify.loadOriginalValues(newVal);
      this.modifiedValue = newVal;
    },
    settings: {
      deep: true,
      handler(val) {
        let tags;
        if (
          val &&
          val.whitelist &&
          JSON.stringify(val.whitelist) !== JSON.stringify(this.tagify.settings.whitelist)
        ) {
          const difference = this.tagify.settings.whitelist.filter(
            (x: any) => !val.whitelist.includes(x)
          );
          tags = this.tagify.getCleanValue();
          for (let x = 0; x < difference.length; x++) {
            if (this.modifiedValue) {
              const tagindex = this.tagify.getTagIndexByValue(difference[x].value)[0];
              if (tagindex !== undefined) {
                const formattedTag = `[[{"value":"${tags[tagindex].value}","text":"${tags[tagindex].text}","title":"${tags[tagindex].title}","style":"${tags[tagindex].style}","prefix":"${tags[tagindex].prefix}"}]]`;
                this.tagify.removeTags(difference[x].value);
                this.modifiedValue = (this.modifiedValue as string).replace(formattedTag, "");
                EventEmitter.emitter.emit(
                  `block-properties-remove-tag-from-field-${this.blockId}`,
                  this.blockId,
                  {
                    id: this.id,
                    value: formattedTag,
                  }
                );
              }
            }
          }
        }
        this.tagify.settings.whitelist = val.whitelist;
        this.tagify.loadOriginalValues(this.modifiedValue);
      },
    },
  },
  mounted() {
    this.tagify = new Tagify(this.$el, this.settings);
  },
});
